import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

import Web3 from "web3";
import NftJson from "../abis/NftJson.json";
import NftStakeJson from "../abis/NftStakeJson.json";
import TokenJson from "../abis/TokenJson.json";

import { useWeb3Context } from "../hooks/web3";


export default function Traning() {

    const { connect, disconnect, connected, providerChainID, checkWrongNetwork, address, provider } = useWeb3Context();


    const [nftContract, setNftContract] = useState(null);
    const [stakingContract, setStakingContract] = useState(null);

    const [mainLoading, setMainLoading] = useState(false);
    const [allNfts, setAllNfts] = useState([]);
    const [stakedNftsId, setStakedNftsId] = useState([])
    const [selectedNftsStake, setSelectedNftsStake] = useState([]);
    const [selectedNftsUnstake, setSelectedNftsUnstake] = useState([]);

    const [stakeBalance, setStakeBalance] = useState('?');
    const [nextReward, setNextReward] = useState('?');
    const [roi, setRoi] = useState("1 token/10 sec"); 

    const [noticeToShow, setNoticeToShow] = useState("Please connect wallet to Stake/UnStake NFT");


    useEffect(() => {
        if (connected) {
            loadData();
        }
    }, []);

    useEffect(() => {
        if (connected) {
            if (providerChainID == 4) {
                loadData();
            } else {
                checkWrongNetwork()
            }
        }
    }, [connected]);

    const _connectToAccount = async () => {
        if (!connected) {
            setNoticeToShow('Connecting...');
            connect()
        }
    }

    const loadDataAgain = () => {
        setSelectedNftsStake([]);
        setSelectedNftsUnstake([]);
        loadData();
    }

    const loadData = async () => {
        setNoticeToShow('loading...');

        setMainLoading(true);
        const web3 = new Web3(provider);
        window.web3 = web3;
        const networkAddress = '0x358F156001675407C73eB316F468B360dbea7916';
        const _nftContract = await new web3.eth.Contract(NftJson.abi, networkAddress);
        setNftContract(_nftContract);

        const stakeNetworkAddress = '0x1faB8922296Dda350a128B3D31913A453ab3309d';
        const _stakeContract = await new web3.eth.Contract(NftStakeJson.abi, stakeNetworkAddress);
        setStakingContract(_stakeContract);

        var _totalNfts = await _nftContract.methods.balanceOf(address).call();

        console.log("totalNFts", _totalNfts.toString());

        let _allNftsArray = [];
        let _stakedNftsId = [];
        var _idArray = 0;
        for (let i = 0; i < _totalNfts.toString(); i++) {
            let _nftId = await _nftContract.methods.tokenOfOwnerByIndex(address, i).call();
            let _isStaked = await _stakeContract.methods.getIsNftStakedByUser(_nftId.toString()).call();

            let _nftUri = await _nftContract.methods.tokenURI(_nftId.toString()).call();
            var _jsonUrl = "https://gateway.pinata.cloud/ipfs/" + _nftUri.substring(7);
            var _imageUrl = '';

            await fetch(_jsonUrl)
                .then((response) => response.json())
                .then((responseJson) => {
                    _imageUrl = "https://gateway.pinata.cloud/ipfs/" + responseJson.image.substring(7);
                })

            let _item = {
                _idArray: _idArray,
                id: _nftId.toString(),
                imgUrl: _imageUrl,
                staked: _isStaked,
                selected: false
            }
            _idArray += 1;
            _allNftsArray.push(_item)
            if (_isStaked) {
                _stakedNftsId.push(_nftId.toString());
            }
        }
        setAllNfts(_allNftsArray);
        setStakedNftsId(_stakedNftsId);

        // loading all values
        const tokenNetworkAddress = '0xdAA3EDF685D502Aa624aA36b5BBCB8645dFe008C';
        const _tokenContract = await new window.web3.eth.Contract(TokenJson.abi, tokenNetworkAddress);
        let _balance = await _tokenContract.methods.balanceOf(address).call();
        let _decemals = await _tokenContract.methods.decimals().call();
        setStakeBalance((_balance.toString() / 10 ** _decemals.toString()).toFixed(3));

        let _timeForOneToken = await _stakeContract.methods.getTimeForOneTokenReward().call();
        var currentTime = Math.floor(Date.now() / 1000);
        let _allStakedNfts = _allNftsArray.filter(item => item.staked === true);
        console.log("all staked", _allStakedNfts);
        let _nextReward = 0;
        for (let i = 0; i < _allStakedNfts.length; i++) {
            let _nft = _allStakedNfts[i];
            let _nftDetails = await _stakeContract.methods.getNftStakedByUser(_nft.id).call();
            let _staketime = _nftDetails.lastRewardClaimTime.toString();
            let _tokens = ((currentTime-_staketime) / _timeForOneToken);
            _nextReward += _tokens;
        }
        setNextReward(_nextReward.toFixed(3));

        setMainLoading(false);
        setNoticeToShow('Connected, Now you can Stake/UnStake');
    }



    const forStakeArray = (_id) => {
        var _allnfts = allNfts;
        var _obj = _allnfts[_id];
        var _demoArray = selectedNftsStake;

        if (_obj.selected) {
            _obj.selected = false;
            setSelectedNftsStake(_demoArray.filter(item => item !== _obj.id));
        } else {
            _obj.selected = true;
            setSelectedNftsStake(_demoArray => [..._demoArray, _obj.id]);
        }
        _allnfts[_id] = _obj;
        setAllNfts(_allnfts);
    }

    const forUnStakeArray = (_id) => {
        var _allnfts = allNfts;
        var _obj = _allnfts[_id];
        var _demoArray = selectedNftsUnstake;

        if (_obj.selected) {
            _obj.selected = false;
            setSelectedNftsUnstake(_demoArray.filter(item => item !== _obj.id));
        } else {
            _obj.selected = true;
            setSelectedNftsUnstake(_demoArray => [..._demoArray, _obj.id]);
        }
        _allnfts[_id] = _obj;
        setAllNfts(_allnfts);
    }



    const stakeOne = async (_id) => {
        await stakingContract.methods.stakeNFT(_id)
            .send({ from: address })
            .on('confirmation', (confirmationNumber, receipt) => {
                console.log(receipt);
                loadDataAgain();
            })
            .on('error', function (error, receipt) {
                console.log(error);
                setNoticeToShow(error.message);
                loadDataAgain();
            });
    }

    const unStakeOne = async (_id) => {
        await stakingContract.methods.unStakeNFT(_id)
            .send({ from: address })
            .on('confirmation', (confirmationNumber, receipt) => {
                console.log(receipt);
                loadDataAgain();
            })
            .on('error', function (error, receipt) {
                console.log(error);
                setNoticeToShow(error.message);
                loadDataAgain();
            });
    }

    const stakeArray = async (_id) => {
        await stakingContract.methods.stakeNFTarray(selectedNftsStake)
            .send({ from: address })
            .on('confirmation', (confirmationNumber, receipt) => {
                console.log(receipt);
                loadDataAgain();
            })
            .on('error', function (error, receipt) {
                console.log(error);
                setNoticeToShow(error.message);
                loadDataAgain();
            });
    }

    const unStakeArray = async () => {
        await stakingContract.methods.unStakeNFTarray(selectedNftsUnstake)
            .send({ from: address })
            .on('confirmation', (confirmationNumber, receipt) => {
                console.log(receipt);
                loadDataAgain();
            })
            .on('error', function (error, receipt) {
                console.log(error);
                setNoticeToShow(error.message);
                loadDataAgain();
            });
    }

    const claimReward = async () => {
        console.log(stakedNftsId)
        await stakingContract.methods.claimRewardarray(stakedNftsId)
            .send({ from: address })
            .on('confirmation', (confirmationNumber, receipt) => {
                console.log(receipt);
                loadDataAgain();
            })
            .on('error', function (error, receipt) {
                console.log(error);
                setNoticeToShow(error.message);
                loadDataAgain();
            });
    }

    return (
        <main id="club-main">
            <section id="planmenu" class="sbfc-bg" style={{ backgroundImage: "url(images/hero-bg-1.png)" }}>
                <div class="container">
                    <div class="text-center sbfc-large-icon">
                        <img class="img-fluid" src="images/hero-bg.png" alt="" />
                    </div>
                </div>
            </section>
            <section class="content-thumb-wrapp">
                <div class="container">

                    <div id="salesoon" class="salesoon-bg">

                        {connected
                            ?
                            <div id="mint" class="mint"  >
                                <div class="row align-items-center">
                                    <div class="col-lg-4">
                                        <div class="cw-listing">
                                            <ol>
                                                <li>CONNECT WALLET</li>
                                                <li>SELECT QTY</li>
                                                <li>PRESS "Stake/UnStake"</li>
                                            </ol>
                                        </div>
                                    </div>
                                    <div class="col-lg-4">

                                        <div class="cw-button text-center mb-4" onClick={disconnect}>
                                            <button class="btn btn-outline-dark" >DisConnect</button>
                                        </div>
                                        <div className="text-center">
                                            <p class=" text-dark">{noticeToShow}</p>
                                        </div>
                                    </div>
                                    <div class="col-lg-4">
                                        <div class="cw-copy">
                                            <p><b>Stake and Earn Rewards</b></p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                            :
                            <div id="mint" class="mint"  >
                                <div class="row align-items-center">
                                    <div class="col-lg-4">
                                        <div class="cw-listing">
                                            <ol>
                                                <li>CONNECT WALLET</li>
                                                <li>SELECT QTY</li>
                                                <li>PRESS "Stake/UnStake"</li>
                                            </ol>
                                        </div>
                                    </div>
                                    <div class="col-lg-4">

                                        <div class="cw-button text-center mb-4" onClick={_connectToAccount}>
                                            <button class="btn btn-outline-dark">Connect</button>
                                        </div>
                                        <div className="text-center">
                                            <p class=" text-dark">{noticeToShow}</p>
                                        </div>
                                    </div>
                                    <div class="col-lg-4">
                                        <div class="cw-copy">
                                            <p><b>Stake and Earn Rewards</b></p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        }

                    </div>
                </div>
            </section>

            <section class="nfts-wrapp">
                <div class="container">
                    <div class="nfts-box">
                        <div class="row">
                            <div class="col-xl-6 col-lg-5">
                                <div class="nfts-title">
                                    <h2>MY NFTS</h2>
                                </div>
                                <div class="mt-4 nfts-dolls">
                                    <div class="nfts-lox lox-one"><strong>YOUR STAKE BALANCE</strong> <span><b>{stakeBalance} $LOX</b></span></div>
                                    <div class="nfts-lox lox-two"><strong>Next Reward Amount</strong> <span><b>{nextReward} $LOX</b></span></div>
                                    <div class="nfts-lox lox-three"><strong>ROI (14-Day Rate)</strong> <span><b>{roi} $LOX</b></span></div>
                                    <div class="nfts-lox lox-button">
                                        {connected
                                        ?
                                        <button disabled={stakedNftsId.length == 0} class="btn btn-theme" onClick={claimReward}>Claim</button>
                                        :
                                        <button class="btn btn-theme" onClick={_connectToAccount}>Connect</button>
                                    }
                                    </div>
                                    <p class=" text-light">{noticeToShow}</p>
                                </div>
                            </div>
                            <div class="col-xl-6 col-lg-7">
                                <div class="nfts-buttons">
                                    <div class="row">
                                        <div class="col-6">
                                            <button disabled={selectedNftsUnstake.length < 1} class={`btn  ${selectedNftsUnstake.length > 0 ? 'btn-theme' : 'btn-grey'} w-100`} onClick={unStakeArray}>Unstake all {selectedNftsUnstake.length}</button>
                                            {selectedNftsUnstake && selectedNftsUnstake.map((o, key) => {
                                                return (
                                                    <p key={key}>{o}</p>
                                                )
                                            })}
                                        </div>

                                        <div class="col-6">
                                            <button disabled={selectedNftsStake.length < 1} class={`btn  ${selectedNftsStake.length > 0 ? 'btn-theme' : 'btn-grey'} w-100`} onClick={stakeArray}>Stake all {selectedNftsStake.length}</button>

                                            {selectedNftsStake && selectedNftsStake.map((o, key) => {
                                                return (
                                                    <p key={key}>{o}</p>
                                                )
                                            })}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="nfts-rows">
                            <div class="row">
                                {allNfts != [] &&
                                    allNfts.map((nftObj, key) => {
                                        return (
                                            <div key={key} class="col-lg-3 col-md-6 col-6">
                                                <div class="stake-box">
                                                    <div class="stake-thumb">
                                                        <a class="stake-url" >
                                                            <img class="img-fluid" src={nftObj.imgUrl} alt="" />
                                                        </a>
                                                    </div>
                                                    <div class="stake-title d-flex">
                                                        <h3># {nftObj.id}</h3>
                                                        {nftObj.staked
                                                            ?
                                                            <label class="form-check-label" onClick={() => { forUnStakeArray(nftObj._idArray) }}>
                                                                <input class="form-check-input" type="checkbox" />
                                                            </label>
                                                            :

                                                            <label class="form-check-label" onClick={() => { forStakeArray(nftObj._idArray) }}>
                                                                <input class="form-check-input" type="checkbox" />
                                                            </label>
                                                        }
                                                    </div>
                                                    <div class="stake-button">
                                                        {nftObj.staked
                                                            ?
                                                            <button onClick={() => { unStakeOne(nftObj.id) }} class="btn btn-theme" >UNSTAKE</button>
                                                            :
                                                            <button onClick={() => { stakeOne(nftObj.id) }} class="btn btn-theme" >STAKE</button>
                                                        }

                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </main>
    )
}
